* {
  margin: 0;
  padding: 0;
  font-family: Ubuntu;
}

body {
  background-image: url('/ocean.jpg');
  background-repeat: repeat;
}

.container {
  margin: 20px auto;
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.card {
  width: 85%;
  min-height: 150px;
  background: linear-gradient(to bottom right, #ff6a00, #ffe000);
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  box-sizing: border-box;
}

.card h2 {
  text-align: center;
  margin-top: 45px;
  font-weight: 400;
  font-size: 2rem;
}

.card p {
  font-size: 1.1rem;
}

.card p:not(:last-child) {
  margin-bottom: 15px;
}